import React from 'react';
import './item-status-filter.css';


export default class ItemStatusFilter extends React.Component{
    
    buttons = [
        {name:'all', lable:'All'},
        {name:'active', lable:'Active'},
        {name:'done', lable:'Done'}
    ]

    name(name){
        this.setState(()=> {
            const {buttonName} = this.props;
            buttonName(name)
        })
    }

    render(){
       
        const button = this.buttons.map(({name, lable}) => {
            const {buttonActive} = this.props;
            const act = buttonActive == name? true: false;
            const clazz = act ? 'btn-info': 'btn-secondary'
            return (<button 
                            type="button" 
                            className={`btn ${clazz}`} 
                            key={name}
                            onClick={() => this.name(name)}
                        >{lable}
                    </button>)
        })

        return (
            <div className="btn-group search">
                {button}
            </div>
            
        )
    }
};