import React from 'react';
import './searche.css'

export default class SearchPenal extends React.Component {
    
    // push val in app
    addValue = (e) => {
        const val = e.target.value;
        this.setState(()=>{
            const {inputVal} = this.props;
            inputVal(val);
        })
    }
    
    render() {
        
        return (
            <div className="search-block">
                <input placeholder="search" className="search"
                        onChange={this.addValue}></input>
            </div>
    );
    } 
};
