import React from 'react';
import AppHeader from '../app-header';
import SearchPenal from '../searche/';
import TodoLisy from '../todo-list/';
import ItemAddForm from '../item-add-form/'
import ItemStatusFilter from '../item-status-filter/';

export default class App extends React.Component {
    maxId = 100;
    state = {
        todoData : [
            this.createElem('Drink Coffee'),
            this.createElem('Make Coffee'),
            this.createElem('Have Coffee')
        ],
        search : '',
        filter: 'all'
    }
    //create element
    createElem(lable) {
        return {lable, important : false, id: this.maxId++, done:false}
    }
    // create important and done
    todoProparty(arr, id , el){
        const inx = arr.findIndex((el) => el.id === id);
        const oldItem = arr[inx];
        const newItem = {...oldItem};
        newItem[el] = !oldItem[el];
        const newTodoData = [...arr.slice(0, inx),newItem, ...arr.slice(inx + 1)]
        return newTodoData;
    }
    // important
    todoImportant = (id) => {
        this.setState(({todoData}) =>{
           
            return {
                todoData: this.todoProparty(todoData, id, 'important')
            }
        })
        
    }
    // done
    todoDone = (id) => {
        this.setState(({todoData}) =>{
            return {
                todoData: this.todoProparty(todoData, id, 'done')
            }
        })
    }
    // search element
    search = (arr, text) => {
        const newArr = arr.filter((item) => item.lable.indexOf(text) > -1);
        return newArr;
    }
    // add new value for search
    inputVal = (lable) => {
        this.setState({
            search : lable
        })
    }
    // what is button click
    filterButton (arr, name) {
        switch (name) {
            case 'all':
                return arr;
            case 'active':
                return arr.filter((item) => !item.done);
            case 'done':
                return arr.filter((item) => item.done);
        
            default:
                return arr;
        }
    }

    


    render (){
        // get main arr
        const {todoData, search, filter} = this.state;
        // new arr
        const newTodoData = this.filterButton(this.search(todoData,search), filter)
        // delete element 
        const  deleteInx = (id) => {
            this.setState(({todoData}) =>{
                const inx = todoData.findIndex((el) => el.id === id);
                const after = todoData.slice(0, inx);
                const before = todoData.slice(inx + 1);
                const newTodoData = [...after, ...before]
                return { todoData : newTodoData }
            })
        };
        // add new element
        const todoAdd = (text) =>{
            this.setState(({todoData}) => {
                const elem = this.createElem(text)
                const newAddList = [
                    ...todoData, elem
                ]
                return {
                    todoData: newAddList
                }
            })
        }
        // filter important and done  elements
        const todoTrue = this.state.todoData.filter((el) => el.done);
        const toimpoTrue = this.state.todoData.filter((el) => el.important);
        //add filters with buttons
        const  buttonNameValue = (name) => {
            this.setState({
                filter : name
            })
         }
        return (
            <div> 
                <AppHeader 
                        toDo = {todoTrue.length} 
                        impo={toimpoTrue.length} />
                <SearchPenal
                        inputVal = {this.inputVal}/>
                <ItemStatusFilter
                        buttonName = {buttonNameValue}
                        buttonActive = {this.state.filter}/>
                <TodoLisy 
                        todos = {newTodoData}
                        toDelete = {deleteInx}
                        onTodoImportant = {this.todoImportant}
                        onTodoDone = {this.todoDone} />
                <ItemAddForm
                        toAdd = {todoAdd}/>
            </div>
            );
    }
}

