import React from 'react';
import './app-header.css'


const AppHeader = ({toDo, impo}) => {
    return(
        <div className="app-header">
            <h1>My Todo List</h1>
            <p className="app-header-parag">{toDo} done, {impo} important</p>
        </div>
    );
};

export default AppHeader;