import React from 'react';
import './item-add-form.css'

export default class ItemAddForm extends React.Component {

    state = {
        lable: ''
    }

    onChangeVal = (e) =>{
        this.setState({
            lable: e.target.value
        })
    }

    onSubmitVal = (e) => {
        const {toAdd} = this.props;
        e.preventDefault();
        toAdd(this.state.lable)
        this.setState({
            lable: ''
        })
    }

    render(){
        return (
            <form className="item-add-form" onSubmit={this.onSubmitVal}>
                <input type="text"
                        onChange={this.onChangeVal}
                        value = {this.state.lable} />
                <button
                className="btn btn-info"
                >add</button>
            </form>
            
        )
    }
} 
