import React from 'react';
import './todo-list-item.css'

export default class TodoListItem extends React.Component{
    
   
    render(){
        const { lable, toDelete, onTodoImportant, onTodoDone, important, done} = this.props
        const liStyle = {
            color: important? 'tomato' : 'black'
        };
        
        let todoClass = 'todoListItem';
        if(done){
            todoClass += ' doneMy'
        }

       return (
           <div className={todoClass}>
                <span 
                    className="spanTodoItem"
                    style={liStyle}
                    onClick={onTodoDone}>
                    { lable }
                </span>
                <div className="btnTodoItem">
                    <button type="button"
                            className="btn btn-outline-success btnFirst"
                            onClick = {onTodoImportant}>
                        <i className="fa fa-exclamation"/>
                    </button>
                    <button type="button"
                            className="btn btn-outline-danger btnSecond"
                            onClick = {toDelete}>
                        <i className="fa fa-trash-o"/>
                    </button>
                </div>
                
          </div>
       
       )
    };
}

